/*
 * Transforma una rese√±a de Strapi al formato que espera el componente CardsCarousel
 */
function transformResenaToReviewItem(resena: ResenaFromStrapi): ReviewItem {
    const { authorName, image, review, footer } = resena;
    return {
        name: authorName || "An√≥nimo",
        review,
        footer: footer || "",
        image: image?.url ?? DEFAULT_AVATAR,
    };
}

/*
 * Obtiene las rese√±as desde Strapi
 * @param limite - N√∫mero m√°ximo de rese√±as a obtener (default: 10)
 */
export async function getReviews(limit = 10): Promise<ReviewItem[]> {
    try {
        // Montamos la query string
        const qs = {
            populate: "image",
            sort: "createdAt:desc",
            "pagination[pageSize]": limit.toString(),
        };

        const data = await strapiQuery<ResenaResponse>("resenas", {
            params: qs,
        });

        if (!data?.data) return FALLBACK_REVIEWS;
        return data?.data.map(transformResenaToReviewItem);
    } catch (err) {
        console.error("Error al obtener rese√±as desde Strapi:", err);
        return FALLBACK_REVIEWS;
    }
}
