"use client";

import {
    Card,
    CardHeader,
    CardContent,
    CardFooter,
} from "@/components/ui/card";

import {
    Carousel,
    CarouselContent,
    CarouselItem,
    CarouselNext,
    CarouselPrevious,
} from "@/components/ui/carousel";
import { getReviews } from "@/lib/strapi/review";

import { ReviewItem } from "@/types/resena";
import Image from "next/image";
import { useEffect, useState } from "react";

const CardsCarousel = () => {
    const [reviews, setReviews] = useState<ReviewItem[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchReviews = async () => {
            try {
                const data = await getReviews(8); // Obtener m√°ximo 8 rese√±as
                setReviews(data);
            } catch (error) {
                console.error("Error cargando rese√±as:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchReviews();
    }, []);

    if (loading) {
        return (
            <div className="flex justify-center items-center h-[360px]">
                <div className="text-gray-500">Cargando rese√±as...</div>
            </div>
        );
    }

    return (
        <Carousel
            opts={{
                align: "start",
                slidesToScroll: 1,
                loop: true,
            }}
            className="w-full relative">
            <CarouselContent className="-ml-4 flex mt-6">
                {reviews.map((item, index) => (
                    <CarouselItem
                        key={index}
                        className="md:basis-1/2 lg:basis-1/3 xl:basis-1/4 flex-shrink-0">
                        <Card
                            className="group p-2 h-[360px] bg-white/90 dark:bg-gray-900/80 
                         border border-gray-200 dark:border-gray-700 
                         rounded-xl shadow-md transition-all duration-500 ease-in-out 
                         transform hover:-translate-y-2 hover:rotate-[0.5deg] hover:shadow-xl">
                            <CardHeader className="flex flex-col items-center gap-1">
                                <div className="relative w-24 h-24 rounded-full overflow-hidden border-4 border-yellow-300 dark:border-yellow-500 shadow-md">
                                    <Image
                                        src={item.image}
                                        alt={item.name}
                                        className="object-cover"
                                        fill
                                    />
                                </div>
                                <h3 className="text-xl font-semibold text-gray-800 dark:text-gray-100">
                                    {item.name}
                                </h3>
                                <p className="text-xs text-gray-500 dark:text-gray-400 italic">
                                    Rese√±a de Kwik-E-Mart
                                </p>
                            </CardHeader>

                            <CardContent className="mt-1 text-gray-700 dark:text-gray-300 text-sm max-h-[150px] overflow-y-auto scrollbar-thin scrollbar-track-transparent">
                                {item.review}
                            </CardContent>

                            <CardFooter className="mt-auto pt-2 text-xs text-gray-400 dark:text-gray-500">
                                {item.footer}
                            </CardFooter>
                        </Card>
                    </CarouselItem>
                ))}
            </CarouselContent>

            <CarouselPrevious className="absolute left-2 top-1/2 -translate-y-1/2 z-10" />
            <CarouselNext className="absolute right-2 top-1/2 -translate-y-1/2 z-10" />
        </Carousel>
    );
};

export default CardsCarousel;
