// lib/api/api.ts
export interface APISettings<Body = Record<string, unknown>> {
    url: string;
    method?: "GET" | "POST" | "PUT" | "PATCH" | "DELETE";
    headers?: Record<string, string>;
    params?: Record<string, string>;
    body?: Body;
    cls?: boolean;
}

export async function apiFetch<T>(settings: APISettings): Promise<T | null> {
    const {
        url,
        method = "GET",
        params,
        headers = {},
        body,
        cls = false,
    } = settings;

    const queryString = params
        ? `?${new URLSearchParams(params).toString()}`
        : "";

    const options: RequestInit = {
        method,
        headers: {
            ...headers,
        },
        ...(method !== "GET" && body ? { body: JSON.stringify(body) } : {}),
    };

    if (cls) {
        const requestInfo = {
            url: url + queryString,
            options,
        };
        console.log("[APIHelper] Request:", JSON.stringify(requestInfo));
    }

    try {
        const response = await fetch(url + queryString, options);
        if (cls) console.log("[APIHelper] Response Status:", response.status);

        if (!response.ok) {
            const errorData = await response.json();
            throw new Error(errorData.message || `HTTP ${response.status}`);
        }

        if (response.status === 204 || response.status === 201) return null;

        const contentType = response.headers.get("Content-Type");
        if (contentType && contentType.includes("application/json")) {
            return await response.json();
        }

        return null;
    } catch (error) {
        console.error("[APIHelper] Error:", error);
        throw error;
    }
}
