// lib/strapi.ts (server‚Äêonly)
import { apiFetch, APISettings } from '../api/apiHelper';
import { STRAPI_HOST, STRAPI_TOKEN } from './config';

export type StrapiApiSettings = Omit<APISettings, 'url' | 'headers'>;

export async function strapiQuery<T>(
  path: string,
  options: StrapiApiSettings,
): Promise<T | null> {
  if (!STRAPI_HOST || !STRAPI_TOKEN) {
    throw new Error(
      '‚ùå Falta configurar STRAPI_HOST o STRAPI_TOKEN en .env.local',
    );
  }

  const url = `${STRAPI_HOST}/api/${path}`;
  const headers = {
    Authorization: `Bearer ${STRAPI_TOKEN}`,
    'Content-Type': 'application/json',
  };

  const settings = {
    url,
    headers,
    ...options,
  };

  return apiFetch(settings);
}
