"use client";

import { useState, useEffect, useRef } from "react";
import { simpsonsReviews } from "./data";
import { ChevronLeft, ChevronRight } from "lucide-react";

const SimpsonsCarousel = () => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const timeoutRef = useRef<NodeJS.Timeout | null>(null);
    const delay = 5000; // 5 segundos entre cada cambio

    // Avanza al siguiente √≠ndice (con wrap-around)
    const handleNext = () => {
        setCurrentIndex((prev) => (prev + 1) % simpsonsReviews.length);
    };
    // Retrocede al √≠ndice anterior (con wrap-around)
    const handlePrev = () => {
        setCurrentIndex((prev) =>
            prev === 0 ? simpsonsReviews.length - 1 : prev - 1
        );
    };

    // Efecto para que rote autom√°ticamente cada `delay` ms
    useEffect(() => {
        // Limpiamos cualquier timeout previo
        if (timeoutRef.current) clearTimeout(timeoutRef.current);

        timeoutRef.current = setTimeout(() => {
            handleNext();
        }, delay);

        return () => {
            if (timeoutRef.current) clearTimeout(timeoutRef.current);
        };
    }, [currentIndex]);

    return (
        <div className="relative mx-auto overflow-hidden rounded-md pr-6 pl-6 pt-3 pb-3 text-shadow-lg/30">
            {/* Frase actual */}
            <div className="text-center">
                <p className="mb-2 text-sm  italic text-white">
                    ‚Äú{simpsonsReviews[currentIndex].phrase}‚Äù
                </p>
                <span className="block text-sm font-semibold text-amber-300">
                    ‚Äî {simpsonsReviews[currentIndex].character}
                </span>
            </div>

            {/* Botones de navegaci√≥n */}
            <button
                onClick={handlePrev}
                aria-label="Anterior"
                className="absolute left-2 top-1/2 flex h-8 w-8 -translate-y-1/2 items-center justify-center rounded-full bg-gray-700/60 text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-amber-300">
                <ChevronLeft className="h-5 w-5" />
            </button>
            <button
                onClick={handleNext}
                aria-label="Siguiente"
                className="absolute right-2 top-1/2 flex h-8 w-8 -translate-y-1/2 items-center justify-center rounded-full bg-gray-700/60 text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-amber-300">
                <ChevronRight className="h-5 w-5" />
            </button>

            {/* Puntos indicadores */}
            <div className="mt-4 flex justify-center gap-1">
                {simpsonsReviews.map((_, idx) => (
                    <button
                        key={idx}
                        onClick={() => setCurrentIndex(idx)}
                        className={`h-2 w-2 rounded-full transition-colors ${
                            idx === currentIndex
                                ? "bg-amber-400"
                                : "bg-gray-500 hover:bg-gray-400"
                        }`}
                        aria-label={`Ir a la frase ${idx + 1}`}
                    />
                ))}
            </div>
        </div>
    );
};

export default SimpsonsCarousel;
